import React, { useState } from 'react';
import Link from 'next/link';

import { navLinks } from '../constants/constants';

import { useTheme } from '../context/ThemeProviders';

export default function NavBar() {
  const [open, setOpen] = useState(false);

  const topics = navLinks;

  const themeToggle = useTheme();

  const onThemeChange = () => {
    themeToggle.toggle();
  };

  const isDarkMode = themeToggle.mode ? true : false;

  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
        <Link href="/">
          <a rel="noopener" aria-label="space science" className="navbar-brand">Space Science</a>
        </Link>
        <button className="navbar-toggler" type="button" onClick={() => setOpen(!open)} aria-controls="navbarColor03" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon" />
        </button>
        <div className={`collapse navbar-collapse ${open ? 'show' : ''}`}>
          <ul className="navbar-nav ml-auto">
            <li className="nav-item">
              <a title="toggle dark mode" className="nav-link cursor-pointer" onClick={onThemeChange}>
                <i className={`fas fa-2x fa-${isDarkMode ? 'sun' : 'moon'}`}></i>
              </a>
            </li>
            {topics.map((top, index) => (
              <li key={index} className="nav-item active">
                <Link href={`/archive?name=${top.name}&id=${top.id}`}>
                  <a rel="noopener" aria-label={top.label} className="nav-link">{top.label}</a>
                </Link>
              </li>
            ))}
            {/* <li className="nav-item active">
                    <Link href="/about" as="/about">
                        <a className="nav-link">About</a>
                    </Link>
                </li> */}
          </ul>
        </div>
      </nav>
    </>
  );
}
